body {
    font-size: 18px !important;
}

.custom-btn {
    @extend%btn-type-4;
    display: inline-block;
	text-align: center;
	line-height: 55px;
	height: 55px;
	width: 170px;
    margin-top: 16px;
	color: #fff;
	font-size: 18px;
	background-color: $base-color-2;
}

#accordionExample {
	.accordion-item .accordion-button {
		padding: 24px 16px;
		font-size: 24px;
		color: #191919;
		background-color: #fff;
	}
	.accordion-body {
		font-size: 17px;
	}
}

#user-auth-btn {
	position: initial;
	padding: 10px 16px;
	background-color: $base-color;
	color: #fff;
	font-weight: normal;
}

.about-icons {
	z-index: 1;
	width: 80px;
	height: 80px;
	margin: 0 auto;
	margin-bottom: 8px;
	border-radius: 100%;
	background-color: $color-2;

	i {
		color: #fff;
		font-size: 24px;
	}
}